@use 'base/mixins' as m;
@use 'base/variables' as v;

.seccion {
    h2 {
        font-family: v.$fuente_principal;
        font-size: 2rem;
        color: v.$azulMarino;
        border-bottom: 1px solid v.$azulMarino;
        text-align: center;
    }
}

.principal {
    
    margin-left: 5rem;
    height: auto;

    .personalInfo {

        .contenedor-info {
            margin-bottom: 2rem;
            padding: 2rem;
            background-color: #537895;
            background-image: linear-gradient(315deg, #293a47 0%, #0d1827 74%);
            background-position: center center;
            background-size: cover;
            background-repeat: no-repeat;
            box-shadow: 2px 7px 5px v.$gris;
            
    
            .fotoperfil {

                img {
                    display: block;
                    justify-content: center;
                    width: 10rem;
                    border-radius: 1rem;
                    margin-bottom: 2rem;
                    }
               
                @include m.tablet {
                    img {
                        width: 25rem;
                        border-radius: 1rem;
                        margin-bottom: 0;
                        }
                }
            }

            
            
            .informacionPrincipal {
                display: block;

                h3, p {
                    font-family: v.$fuente_principal;
                    font-size: 1.5rem;
                    color: v.$blanco;
                    border-bottom: 1px solid v.$blanco;
                }   
                
               

                @include m.tablet {

                    h3, p {
                        font-size: 2.3rem;
                    }
                }
            }

            @include m.tablet {
                @include m.grid(2, 2rem);
            }
        }

        .informacionSec {
                
            p {
                margin-left: 1.5rem;
                margin-right: 1.5rem;
                margin-bottom: 1rem;
                text-align: center;
                font-size: 1.3rem;
                font-family: v.$fuente_principal;
                color: v.$azulMarino;
            }

            @include m.tablet {
                
                p {
                    text-align: left;
                    font-size: 1.8rem;
                }
            }
        }
    }

    @include m.tablet {
        .seccion {
            h2 {
                font-size: 4rem;
            }
        }
    }    

    @include m.desktop {
            margin-left: 25rem;
    }
}