@use 'base/mixins' as m;
@use 'base/variables' as v;


.sidebar {
    visibility: visible;
    height: 100%;
    background-color: #a38560;
    background-image: linear-gradient(315deg, #e4a150 0%, #e2c483 74%);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    max-width: 5rem;
    overflow-x: hidden;
    transition: 0.5s ease-in-out;
    box-shadow: 0 0 7px 5px v.$gris;

    &.mostrar {
        max-width: 15rem;

        @include m.desktop {
            max-width: 25rem;
        }

        .menu {
            visibility: visible;
        }

        .imagen-menu {
            visibility: hidden;
        }
    }

    &.ocultar {
        visibility: hidden;
    }

    &.bigMenu {
        @include m.desktop {
            max-width: 25rem;
        
                .menu {
                    visibility: visible;
                }
            }
    }
    
    .imagen-menu {
        position: relative;
        left: 50%;
        -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        transform: translateX(-50%);
        height: 3rem;
        width: 3rem;

        img {
            &:hover {
                cursor: pointer;
            }
        }

        @include m.desktop {
            display: none;
        }
    }
    
    .menu {
        visibility: hidden;

        .imagen-logo {
            display: flex;
            justify-content: center;

            &:hover {
                cursor: pointer;
            }
        }
    
        .cerrar-menu {
            position: relative;
            left: 50%;
            -webkit-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
            transform: translateX(-50%);
            height: 2rem;
            width: 2rem;
        
            img {
                &:hover {
                    cursor: pointer;
                }
            }
        
            @include m.desktop {
                display: none;                
            }
        }
        
        .sidebar-nav {
            margin-top: 2rem;
    
            a {
                display: block;
                margin-bottom: 2rem;
                padding: 1.6rem;
                margin: 0 1rem 0 1rem ;
                text-align: center;
                font-size: 1.3rem;
                text-decoration: none;
                color: v.$azulMarino;
                font-family: v.$fuente_headings;
                transition-property: background-color;
                transition-duration: 300ms;
                border-radius: 1rem;
    
                &:hover {
                    background-color: darken(v.$naranja, 5%);
                }
            
                @include m.desktop {
                    font-size: 2rem;
                }
        
            }
        }

        .iconos {
            display: flex;
            justify-content: center;
            position: absolute;
            bottom: 30px;
            width: 100%;

            img {
                padding: .5rem;
            }

            @include m.desktop {
                display: flex;
                justify-content: center;
                position: absolute;
                bottom: 30px;
                width: 100%;

                img {
                    padding: 1rem;
                }
            }
        }
    }
}