@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-formacion {
    margin-bottom: 4rem;
    padding: 2rem;
    background-color: #a38560;
    background-image: linear-gradient(315deg, #e4a150 0%, #e2c483 74%);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: 2px 5px 5px v.$gris;
    
    .formacion {
        border-bottom: 1px solid v.$azulMarino;
        
        p {
            font-family: v.$fuente_textos;
            font-size: 2.2rem;
            color: v.$azulMarino;
            
        }

        span {
            font-weight: bold;
        }

        .fechas {
            font-size: 1.8rem;
        }

        ul {
            li {
                font-family: v.$fuente_textos;
                color: v.$azulMarino;
                margin-bottom: 1.5rem;
                list-style-type: none;
            }
        }
    }
}