html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  background-color: #c5c7ca;
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Bungee", cursive;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

/** Media Queries **/
.sidebar {
  visibility: visible;
  height: 100%;
  background-color: #a38560;
  background-image: linear-gradient(315deg, #e4a150 0%, #e2c483 74%);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 5rem;
  overflow-x: hidden;
  transition: 0.5s ease-in-out;
  box-shadow: 0 0 7px 5px #6b7280;
}
.sidebar.mostrar {
  max-width: 15rem;
}
@media (min-width: 1024px) {
  .sidebar.mostrar {
    max-width: 25rem;
  }
}
.sidebar.mostrar .menu {
  visibility: visible;
}
.sidebar.mostrar .imagen-menu {
  visibility: hidden;
}
.sidebar.ocultar {
  visibility: hidden;
}
@media (min-width: 1024px) {
  .sidebar.bigMenu {
    max-width: 25rem;
  }
  .sidebar.bigMenu .menu {
    visibility: visible;
  }
}
.sidebar .imagen-menu {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  height: 3rem;
  width: 3rem;
}
.sidebar .imagen-menu img:hover {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .sidebar .imagen-menu {
    display: none;
  }
}
.sidebar .menu {
  visibility: hidden;
}
.sidebar .menu .imagen-logo {
  display: flex;
  justify-content: center;
}
.sidebar .menu .imagen-logo:hover {
  cursor: pointer;
}
.sidebar .menu .cerrar-menu {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  height: 2rem;
  width: 2rem;
}
.sidebar .menu .cerrar-menu img:hover {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .sidebar .menu .cerrar-menu {
    display: none;
  }
}
.sidebar .menu .sidebar-nav {
  margin-top: 2rem;
}
.sidebar .menu .sidebar-nav a {
  display: block;
  margin-bottom: 2rem;
  padding: 1.6rem;
  margin: 0 1rem 0 1rem;
  text-align: center;
  font-size: 1.3rem;
  text-decoration: none;
  color: #1d2936;
  font-family: "Bungee", cursive;
  transition-property: background-color;
  transition-duration: 300ms;
  border-radius: 1rem;
}
.sidebar .menu .sidebar-nav a:hover {
  background-color: #966f2b;
}
@media (min-width: 1024px) {
  .sidebar .menu .sidebar-nav a {
    font-size: 2rem;
  }
}
.sidebar .menu .iconos {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 30px;
  width: 100%;
}
.sidebar .menu .iconos img {
  padding: 0.5rem;
}
@media (min-width: 1024px) {
  .sidebar .menu .iconos {
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 30px;
    width: 100%;
  }
  .sidebar .menu .iconos img {
    padding: 1rem;
  }
}

.seccion h2 {
  font-family: "Source Code Pro", monospace;
  font-size: 2rem;
  color: #1d2936;
  border-bottom: 1px solid #1d2936;
  text-align: center;
}

.principal {
  margin-left: 5rem;
  height: auto;
}
.principal .personalInfo .contenedor-info {
  margin-bottom: 2rem;
  padding: 2rem;
  background-color: #537895;
  background-image: linear-gradient(315deg, #293a47 0%, #0d1827 74%);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 2px 7px 5px #6b7280;
}
.principal .personalInfo .contenedor-info .fotoperfil img {
  display: block;
  justify-content: center;
  width: 10rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .principal .personalInfo .contenedor-info .fotoperfil img {
    width: 25rem;
    border-radius: 1rem;
    margin-bottom: 0;
  }
}
.principal .personalInfo .contenedor-info .informacionPrincipal {
  display: block;
}
.principal .personalInfo .contenedor-info .informacionPrincipal h3, .principal .personalInfo .contenedor-info .informacionPrincipal p {
  font-family: "Source Code Pro", monospace;
  font-size: 1.5rem;
  color: #FFFFFF;
  border-bottom: 1px solid #FFFFFF;
}
@media (min-width: 768px) {
  .principal .personalInfo .contenedor-info .informacionPrincipal h3, .principal .personalInfo .contenedor-info .informacionPrincipal p {
    font-size: 2.3rem;
  }
}
@media (min-width: 768px) {
  .principal .personalInfo .contenedor-info {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.principal .personalInfo .informacionSec p {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
  margin-bottom: 1rem;
  text-align: center;
  font-size: 1.3rem;
  font-family: "Source Code Pro", monospace;
  color: #1d2936;
}
@media (min-width: 768px) {
  .principal .personalInfo .informacionSec p {
    text-align: left;
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .principal .seccion h2 {
    font-size: 4rem;
  }
}
@media (min-width: 1024px) {
  .principal {
    margin-left: 25rem;
  }
}

