@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-experiencia {
    margin-bottom: 4rem;
    padding: 2rem;
    background-color: #537895;
    background-image: linear-gradient(315deg, #293a47 0%, #0d1827 74%);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: 2px 5px 5px v.$gris;

    .experiencia {
        border-bottom: 1px solid v.$blanco;
        

        p {
            font-family: v.$fuente_textos;
            font-size: 2.2rem;
            color: v.$blanco;
            span {
                font-weight: bold;
            }
        }

        .fechas {
            font-size: 1.8rem;
        }

        ul {
            li {
                font-family: v.$fuente_textos;
                color: v.$blanco;
                margin-bottom: 1.5rem;
                list-style-type: none;
            }
        }
    }
}