@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-competencias {
    margin-bottom: 4rem;
    padding: 2rem;
    background-color: #a38560;
    background-image: linear-gradient(315deg, #e4a150 0%, #e2c483 74%);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: 2px 5px 5px v.$gris;

    .competencias {
        @include m.tablet {
                @include m.grid(2, 2rem);
        }
        
        .competencia {
            ul {
                li {
                    font-family: v.$fuente_textos;
                    color: v.$azulMarino;
                    margin-bottom: 1.5rem;
                    
                }
            }
        }
    }
}